.game-container {
  width: 600px;
  height: 600px;
  display: flex;
  flex-wrap: wrap;
}
.cell {
  width: 180px;
  height: 180px;
  padding: 5px;
  margin: 5px;
  cursor: pointer;
}
.false {
  background-color: #1565c0;
}
.false:hover {
  background-color: #1976d2;
}
.true {
  background-color: #42a5f5;
}
.true:hover {
  background-color: #64b5f6;
}
